h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

*, :before, :after {
  box-sizing: border-box;
}

a, a:link, a:visited {
  text-decoration: none;
}

:root {
  --primary-text-color: #f8f8f8;
  --secondary-text-color: #b7b7b7;
  --primary-title-color: #fff;
  --primary-bgc: #282828;
  --primary-btn-color: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --primary-ligth-theme-text-color: #282828;
  --secondary-ligth-theme-text-color: #595959;
  --ligth-theme-title-color: #111;
  --ligth-theme-bgc: #fff;
  --primary-ligth-theme-btn-color: linear-gradient( 141.22deg, #ffc226 9.4%, #f84119 91.91% );
  --accent-color: #f87719;
  --black: #000;
  --white: #fff;
}

body {
  color: var(--primary-text-color);
  background-color: var(--primary-bgc);
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

body.ligth-mode {
  background-color: var(--ligth-theme-bgc);
  color: var(--secondary-ligth-theme-text-color);
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

:root {
  --star-size: 32px;
  --star-color: rgba(248, 159, 25, .68);
  --star-background: #f84119;
}

.Stars {
  --percent: calc(var(--rating) / 5 * 100%);
  font-size: var(--star-size);
  font-family: Times;
  line-height: 1;
  display: inline-block;
}

.Stars:before {
  content: "★★★★★";
  letter-spacing: 3px;
  background: linear-gradient( 90deg, var(--star-background) var(--percent), var(--star-color) var(--percent) );
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.hero-gradient {
  height: 380px;
  background: linear-gradient(86.77deg, #111 30.38%, rgba(17, 17, 17, 0) 65.61%);
  padding: 40px 57px 80px 20px;
}

.container-wrapper {
  overflow: hidden;
}

.hero-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-image: url("hero-home-mob1.9476a76e.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero-img.hero-lib {
  background-image: url("hero-lib-mobilezip.dce4d674.jpg");
}

.hero-description-wrap {
  margin-top: 20px;
  margin-bottom: 20px;
}

.hero-description1 {
  height: 99px;
  color: #f8f8f8;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.14;
  display: inline-block;
  overflow-y: scroll;
}

.hero-description2::-webkit-scrollbar {
  width: 1px;
}

.hero-description2::-webkit-scrollbar-thumb {
  background-color: var(--accent-color);
  border-radius: 50%;
}

.hero-description2::-webkit-scrollbar-thumb:hover {
  background: #fff;
}

.hero-description1::-webkit-scrollbar {
  width: 1px;
}

.hero-description1::-webkit-scrollbar-thumb {
  background-color: var(--accent-color);
  border-radius: 50%;
}

.hero-description1::-webkit-scrollbar-thumb:hover {
  background: #fff;
}

.hero-title-wrap {
  margin-bottom: 8px;
}

.hero-title {
  color: #fff;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.18;
}

.hero-description2 {
  height: 60px;
  display: none;
  overflow-y: scroll;
}

.hero-btn.hero-btn-trailer {
  cursor: pointer;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 74px;
  margin-bottom: 40px;
  padding: 12px 24px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.hero-btn.hero-btn-trailer:hover, .hero-btn.hero-btn-trailer:focus {
  background: linear-gradient(323deg, #ffc226 9.4%, #f84119 91.91%);
}

.hero-btn.hero-btn-more {
  background: var(--ligth-theme-title-color);
  text-align: center;
  color: var(--primary-title-color);
  border-radius: 74px;
  padding: 12px 24px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  transition: border .25s linear, border-color .25s linear, background .25s linear, color .25s linear;
  position: relative;
}

.hero-btn.hero-btn-more:before {
  content: "";
  z-index: -1;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 34px;
  position: absolute;
  top: -1px;
  bottom: -1px;
  left: -1px;
  right: -1px;
}

.hero-btn.hero-btn-more:hover {
  background-color: var(--primary-title-color);
  color: var(--accent-color);
}

