.is-trailer-hidden {
  visibility: hidden;
  opacity: 0;
  transition: opacity .5s linear, visibility .5s linear, transform .5s;
  transform: scale(0);
}

.trailer-container {
  width: 100%;
  height: 100%;
  visibility: visible;
  opacity: 1;
  transition: opacity .5s linear, visibility .5s linear, transform .5s;
  transform: scale(1);
}

.trailer-container.is-trailer-hidden {
  visibility: hidden;
  opacity: 0;
  transition: opacity .5s linear, visibility .5s linear, transform .5s;
  transform: scale(0);
}

.is-scroll-block {
  overflow: hidden;
}

.trailer-backdrop {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  z-index: 40;
  opacity: 1;
  visibility: visible;
  background-color: rgba(24, 23, 23, .77);
  justify-content: center;
  align-items: center;
  transition: opacity .5s linear, visibility .5s linear, transform .1s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  transform: scale(1);
}

.trailer-backdrop.is-trailer-hidden {
  visibility: hidden;
  opacity: 0;
  transition: opacity .5s linear, visibility .5s linear, transform .5s;
  transform: scale(0);
}

