.switch {
  width: 3.5em;
  height: 2em;
  font-size: 17px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: orange;
  border-radius: 30px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 1.4em;
  width: 1.4em;
  background-color: orange;
  border-radius: 20px;
  transition: all .4s;
  animation: anima1 .3s linear;
  position: absolute;
  bottom: .3em;
  left: .3em;
  box-shadow: inset 2px -2px 0 1.8px #fff;
}

@keyframes anima1 {
  0% {
    transform: translateX(1.5em);
  }

  80% {
    transform: translateX(-.3em);
  }

  100% {
    transform: translateX(0);
  }
}

.switch input:checked + .slider:before {
  box-shadow: none;
  background-color: #fff;
  animation: anima .3s linear;
  transform: translateX(1.5em);
}

@keyframes anima {
  0% {
    transform: translateX(0);
  }

  80% {
    transform: translateX(1.6em);
  }

  100% {
    transform: translateX(1.4em);
  }
}

